
Template.__checkName("layout");
Template["layout"] = new Template("Template.layout", (function() {
  var view = this;
  return [ HTML.Raw('<header>\n		<div class="container">\n			<h1>My Meteor Single Page App</h1>\n			<ul>\n				<li>\n					<a href="/">Home</a>\n				</li>\n				<li>\n					<a href="/about">About</a>\n				</li>\n			</ul>	\n		</div>\n	</header>\n\n	'), HTML.DIV({
    "class": "container"
  }, "\n		", HTML.MAIN("\n			", Spacebars.include(view.lookupTemplate("yield")), "\n		"), "\n	") ];
}));

Template.__checkName("notFound");
Template["notFound"] = new Template("Template.notFound", (function() {
  var view = this;
  return HTML.Raw('<div class="center">\n		<h1>Nothing here!</h1><br>\n		<h2>You hit a page which doesn\'t exist!</h2>\n	</div>');
}));

Template.__checkName("loading");
Template["loading"] = new Template("Template.loading", (function() {
  var view = this;
  return HTML.Raw('<div class="center">\n		<h1>Loading</h1>\n	</div>');
}));
