
Template.__checkName("postInList");
Template["postInList"] = new Template("Template.postInList", (function() {
  var view = this;
  return HTML.DIV({
    "class": "postListItem"
  }, "\n		", HTML.H2(HTML.A({
    href: function() {
      return [ "posts/", Spacebars.mustache(view.lookup("slug")) ];
    }
  }, Blaze.View("lookup:title", function() {
    return Spacebars.mustache(view.lookup("title"));
  }))), "\n		", HTML.P(Blaze.View("lookup:description", function() {
    return Spacebars.mustache(view.lookup("description"));
  })), "\n		", HTML.DIV({
    "class": "footer"
  }, "\n			", HTML.TIME({
    datetime: function() {
      return Spacebars.mustache(view.lookup("formatTime"), view.lookup("timeCreated"), "iso");
    }
  }, "Posted ", Blaze.View("lookup:formatTime", function() {
    return Spacebars.mustache(view.lookup("formatTime"), view.lookup("timeCreated"), "fromNow");
  }), " by ", Blaze.View("lookup:author", function() {
    return Spacebars.mustache(view.lookup("author"));
  })), "\n		"), "\n	");
}));
