
Template.__checkName("post");
Template["post"] = new Template("Template.post", (function() {
  var view = this;
  return [ HTML.H1(Blaze.View("lookup:title", function() {
    return Spacebars.mustache(view.lookup("title"));
  })), "\n	", HTML.H2(Blaze.View("lookup:description", function() {
    return Spacebars.mustache(view.lookup("description"));
  })), "\n\n	", HTML.SMALL("\n		Posted ", Blaze.View("lookup:formatTime", function() {
    return Spacebars.mustache(view.lookup("formatTime"), view.lookup("timeCreated"), "fromNow");
  }), " by ", Blaze.View("lookup:author", function() {
    return Spacebars.mustache(view.lookup("author"));
  }), "\n	"), "\n\n	", HTML.DIV({
    "class": "postContent"
  }, "\n		", Spacebars.include(view.lookupTemplate("markdown"), function() {
    return [ "\n		", Blaze.View("lookup:text", function() {
      return Spacebars.mustache(view.lookup("text"));
    }), "\n		" ];
  }), "\n	") ];
}));
